import axios, { get_login_token, get_user_data, LOGIN_TOKEN_KEY, set_login_token, set_user_data} from "../axios.js";

export default {
    namespaced: true,
    state () {
        return {
            profile: {},
        }
    },
    mutations: {
        getProfile(state, profile)
        {
            state.profile = profile
        },
    },
    actions: {
        getProfile({commit}, param = {}) {
            axios().get(`/api/my-profile/${param.profileId}`)
            .then( response => {
                commit('getProfile', response.data);
                console.log('sdf', response.data)
            });
        },
    }
}