import axios from "../axios";
// import axios, {$axios, catch_handler, then_handler} from "../token";

export default {
    namespaced: true,
    state () {
        return {
            feedbacks: {},
        }
    },
    mutations: {
        getFeedbacks(state, feedbacks)                
        {
            state.feedbacks = feedbacks;
        }
    },
    actions: {
        getFeedbacks({commit}, param = {}) {
            axios().get('/api/feedback/create?appointment_id=50', {
                params:  param
              })
            .then( response => {
                console.log(param.appointmentId)
                commit('getFeedbacks', response.data);
                console.log(response.data)
            });
        }

    }
}