import { data } from "autoprefixer";
import axios, { get_login_token, get_user_data, LOGIN_TOKEN_KEY, set_login_token, set_user_data} from "../../axios.js";

export default {
    namespaced: true,
    state () {
        return {
            user:  get_user_data() || { },
            isLoggedIn: get_login_token() ? true : false
        }
    },
    mutations: {
        setLogin( state, data  ){
            if( data === null ) {
                state.user = false;
            } else {

                set_login_token( data.token );
                set_user_data( data.user  );

                state.user = data.user
                state.isLoggedIn = true
            }
        },
    },
    actions: {
       async findUser({commit}, param = {}) {

            try{
                const response = await axios().post( '/api/register', param.data );
                  if( response ) {                
                    return  commit( "setLogin", response.data );
                  }
    
            } catch( r ){
                set_login_token(false);
                if( r.response ) {
                    commit( "setLogin", null );    
                }
            }

            return response;
        }
    },
    
}
